export default {
	data() {
		return {
			userName: '',
			userIcon: false,
		}
	},
	onLoad: function(options) {
		this.userName = options.userName
		if (this.userName.length > 0) {
			this.userIcon = true;
		} else {
			this.userIcon = false;
		}
	},
	methods: {
		tapUser: function(e) {
			this.userName = e.target.value;
			if (e.target.value.length > 0) {
				this.userIcon = true;
			} else {
				this.userIcon = false;
			}
		},
		clearUserIcon: function() {
			this.userName = '';
			this.userIcon = false;
		},
		formSubmit: function(e) {
			if (this.userName.length < 1) {
				uni.showToast({
					icon: 'none',
					title: '请输入昵称！',
					duration: 1000
				});
			} else if (!(/^[\u4E00-\u9FA5A-Za-z0-9_]+$/).test(this.userName)) {
				uni.showToast({
					icon: 'none',
					title: '请输入昵称有误！',
					duration: 1000
				});
			} else {
				uni.request({
					url: this.$Url + '/api/v1/change/user/name', //仅为示例，并非真实接口地址。
					method: 'POST',
					data: {
						token: uni.getStorageSync('token'),
						username: this.userName
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: (res) => {
						if (res.data.code == 200) {
							uni.showToast({
								title: res.data.msg,
								duration: 1000
							});
						} else {
							uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 1000
							});
						}
					},
				})
			}
		}
	}
}
